* {
	box-sizing: border-box;
}

@transparent_black: rgba(0, 0, 0, 0.8);
@h1_color: #B2004D;
h1, h2, h3, h4, h5 {
	font-family: 'Roboto', sans-serif;
	font-weight: normal;
}

.std_content_paddings {
	padding-left: 10.15%;
	padding-right: 10.15%;
	padding-top: 4.4%;
}

.gradiente_block {
	background-image: -moz-linear-gradient(#FFF, #E6E7E8);
	background-image: -webkit-linear-gradient(#FFF, #E6E7E8);
	background-image: linear-gradient(#FFF, #E6E7E8);
	box-shadow: 0 5px 14px rgba(0, 0, 0, 0.4);
	border: 1px solid #B2B2B2;
	border-radius: 6px;
}

.orange_border {
	position: relative;
	&:before, &:after {
		position: absolute;
		height: 5px;
		width: 100%;
		top: 0;
		left: 0;
		content: "";
		background: url(../img/icons-s51a623a2b8.png) 0 -1074px;
	}
	&:after {
		bottom: 0;
		top: auto;
	}

}

.clearfix {
	zoom: 1;
	&:before, &:after {
		content: "";
		display: table;
	}
	&:after {
		clear: both;
	}
}

.pr0 {
	padding-right: 0;
}

input[type="radio"] {
	text-indent: -9999px;
	position: relative;
	width: 14px;
	height: 14px;
	&:before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		background: #FFF;
		border-radius: 50%;
	}
	&:after {
		border-radius: 50%;
	}
}

.h2 {
	font-size: 15px;
	padding-left: 70px;
	height: 58px;
	line-height: 58px;
}

.btn/*, .header .login*/  {
	padding: 0 24px;
	height: 21px;
	text-decoration: none;
	line-height: 22px;
}

.cat_item {
	text-align: center;
	width: auto !important;
	span {
		position: static !important;
		display: block !important;
		background: none !important;
		margin-left: 0 !important;
		width: auto !important;
		font-size: 14px;
		color: #808080 !important;
		&:before {
			display: none !important;
		}
	}
}

.offer {
	.offer__text {
		padding: 0 25px 0 36px;
	}
	.offer__year {
		font-size: 37px;
		padding-top: 21px;
	}
	.offer__title {
		font-size: 44px;
	}
	.offer__more {
		font-size: 21px;
	}
}

.entry.entry_big {
	.entry__text {
		padding: 33px 40px 69px 30px;
		.entry__title {
			font-size: 30px;
		}
	}
}

