// Choosed List

.choosed_list_container {
	position: fixed;
	bottom: 0;
	right: 90px;
	z-index: 100;
	background-color: #000;
	-webkit-border-radius: 12px 12px 0 0;
	-moz-border-radius: 12px 12px 0 0;
	border-radius: 12px 12px 0 0;
	width: 700px;
	padding: 0 12px 0 10px;
	&.collapsed {
		.choosed_list_content {
			display: none;
		}
	}
	.choosed_list_header {
		padding: 17px 0 12px;
		.heading {
			color: #E5E5E5;
			font-size: 16px;
			display: inline-block;
			margin-left: 47px;
			word-spacing: 2px;
			float: left;
		}
		.actions {
			float: right;
			margin-right: 22px;
			margin-top: 2px;
			a {
				display: inline-block;
				width: 20px;
				height: 14px;
				border-style: solid;
				border-width: 1px;
				margin-left: 27px;
				&.action_collapse {
					border-width: 0;
					border-bottom-width: 3px;
					border-bottom-color: #E5E5E5;
				}
				&.action_show {
					border-color: #E5E5E5;
					border-top-width: 3px;
				}
			}
		}
	}
	.choosed_list_content {
		background-color: #FFF;
		padding: 30px 42px 30px 45px;
		.choosed_product {
			border-bottom: 1px solid #B2B2B2;
			padding-bottom: 4px;
			margin-bottom: 1px;
			.choosed_product_pic {
				width: 62px;
				height: 54px;
				float: left;
				text-align: center;
				margin-right: 8px;
				padding: 2px;
				img {
					display: inline-block;
					max-height: 100%;
					max-width: 100%;
				}
			}
			.choosed_product_text {
				width: 450px;
				float: left;
				padding-top: 8px;
				p {
					font-size: 18px;
					color: @h1_color;
					line-height: 1;
				}
				span {
					font-size: 14px;
					color: #383838;
				}
			}
			.choosed_product_act {
				width: 36px;
				float: right;
				margin-top: 10px;
				.checked, .checked_plus {
					width: 36px;
					height: 36px;
					background: url(../img/icons/checked2.png);
					cursor: pointer;
					position: relative;
					span {
						display: none;
						position: absolute;
						background: #333;
						font-size: 11px;
						color: #E5E5E5;
						left: 20px;
						top: 10px;
						padding: 2px 4px;
					}
					&:hover span {
						display: inline-block;
					}
				}
				.checked_plus {
					background: url(../img/icons/checked3.png);
				}
			}
		}
	}
}

//\\ End Choosed list

// Generator Form

.generator_form {
	padding: 41px 5% 69px;
	margin-bottom: 104px;
	.text_fields {
		border-bottom: 1px solid #999;
		padding-bottom: 64px;
		margin-bottom: 46px;
		.field {
			position: relative;
			.text_field {
				font-family: mr, sans-serif;
				font-size: 20px;
				height: 34px;
				width: 76.95%;
				float: left;
				border-bottom: 1px dotted #000;
				background: none;
				margin-right: 3.3%;
			}
			span {
				font-size: 21px;
				color: #383838;
				position: absolute;
				top: 13px;
				right: 0;
				bottom: 0;
				width: 23.05%;
				padding: 0 0 0 41px;
			}
		}
	}
	.radios {
		margin-bottom: 38px;
		display: table;
		width: 100%;
		.radios_section {
			display: table-cell;
			padding-top: 20px;
			padding-bottom: 17px;
			&.radios_section1 {
				width: 32.8%;
				border-right: 1px solid #999;
			}
			&.radios_section2 {
				width: 34.4%;
				border-right: 1px solid #999;
			}
			&.radios_section2,
			&.radios_section3 {
				padding-left: 19px;
			}

			.radios_section_row {
				margin-bottom: 1.5px;
				position: relative;
				.inputs {
					width: 64px;
					float: left;
					line-height: 20px;
					text-align: right;
					margin-right: 11px;
				}
				.radio {
					vertical-align: middle;
					display: inline-block;
				}
				.text {
					display: block;
					border: 1px solid #999;
					max-width: 100%;
					height: 21px;
				}
			}
			label {
				font-size: 18px;
				color: #383838;
				line-height: 20px;
				margin-top: 4px;
			}
		}
	}
	.buttons {
		width: 700px;
		margin: 0 auto;
		.btn {
			line-height: 40px;
			font-size: 16px;
			text-transform: uppercase;
			padding: 0 0 36px;
			width: 270px;
			text-align: center;
			font-family: mr,"Myriad Pro",Arial,sans-serif;
			i {
				margin-top: -5px;
			}
		}
		.excel_btn {
			float: left;
			i {
				display: inline-block;
				width: 25px;
				height: 25px;
				background: url(../img/icons-s51a623a2b8.png) -75px -561px;
				margin-right: 7px;
				vertical-align: middle;
			}
		}
		.pdf_btn {
			float: right;
			i {
				display: inline-block;
				width: 27px;
				height: 28px;
				background: url(../img/icons-s51a623a2b8.png) -40px -560px;
				margin-right: 7px;
				vertical-align: middle;
			}
		}
	}
}

//\\ End Generator Form
