	.appeal_content {
		padding-bottom: 53px;
		border-top: 0;
		border-radius: 0 0 6px 6px;
		article {
			.std_content_paddings();
			padding-left: 430px;
		}
		h1 {
			.lf();
			width: 250px;
			color: @h1_color;
			line-height: 1;
			float: left;
			margin-left: -288px;
			margin-bottom: 35px;
			margin-top: -3px;
			letter-spacing: -3px;
		}
		p {
			font-size: 16px;
			line-height: 19px;
			margin-bottom: 16px;
		}
		blockquote {
			margin-left: -288px;
			float: left;
			font-family: 'Roboto', sans-serif;
		}
	}
	&.toggled {
		.appeal_content {
			position: absolute;
			right: 0;
			top: 0;
			bottom: 0;
			color: #FFF;
			background: @transparent_black;
			width: 33.1%;
			border: 0 none;
			box-shadow: 0 0 0;
			border-radius: 0;
			article {
				padding: 61px 70px 85px 32px;
			}
			h1 {
				float: none;
				width: auto;
				color: #FFF;
				margin-left: 0;
			}
		}
	}
}

.toggled {
	.hided_content {
		display: none;
	}
}

